<template>
  <div class="map_box">
    <!-- <div @click="zoomHanlder(0)">缩小</div> -->
    <!-- <div @click="zoomHanlder(2)">恢复默认</div> -->
    <!-- <div @click="zoomHanlder(1)">放大</div> -->
    <div id="nanzhi"></div>
  </div>
</template>

<script>
import echartJs from 'echarts'
import UsaJson from '@/data/usa'
import UsaData from '@/data/usaData'

export default {
  name: 'myMap',
  data() {
    return {
      nanzhiEchart: null
    }
  },
  mounted() {
    this.initMap()
  },
  methods: {
    zoomHanlder(val) {
      console.log('nanzhiEchart', this.nanzhiEchart.series[0].zoom)
    },
    initMap() {
      const idStr = 'nanzhi'
      document.getElementById(idStr).removeAttribute('_echarts_instance_')
      echartJs.registerMap('USA', UsaJson)
      this.nanzhiEchart = echartJs.init(document.getElementById(idStr));
      let option = {
        tooltip: {
          trigger: 'item',
        },
        series: [
          {
            type: 'map',
            // roam: true,
            map: 'USA',
            zoom: 1,
            label: {show: true},
            // center: [-50, 50],
            data: UsaData,
            markPoint: {
              // symbol: 'rect',
              data: [
                {name: 'Alabama', value: 46, coord: [-100, 50]},
              ]
            }
          }
        ]
      }
      this.nanzhiEchart.setOption(option);
      window.removeEventListener("resize", () => this.nanzhiEchart.resize())
      window.addEventListener("resize", () => this.nanzhiEchart.resize());
    }
  },
  beforeDestroy() {
    this.nanzhiEchart && this.nanzhiEchart.dispose()
  }
}
</script>

<style lang="less" scoped>
.map_box {
  height: calc(~"100% - 90px");
  width: 100%;
  #nanzhi {
    height: 100%;
    width: 100%;
  }
}
</style>